.button {
    display: inline-flex;
    padding: 5px 24px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    transition: all 0.3s;
    color: #FFF;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    border-radius: 5px;
    cursor: pointer;
}

.disabled {
    background-color: #06C;
    color: #002E5C;
    opacity: 0.5;
}

.secondary {
    color: #06C;
    border: 1px solid #06C;
    background-color: transparent;
}

.default {
    background-color: #06C;
    border-color: transparent;
}

.red {
    background: #CC1439;
    border-color: transparent;
}

.default:hover {
    background: #0080FF;
}

.red:hover {
    background: #FF1A47;
}

.secondary:hover {
    background: #E5F0FA;
}


.default:active {
    background: #0059B2;
}

.secondary:active {
    background: #C9E5FF;
}

.red:active {
    background: #B21232;
}

.button:disabled {
    background: #004D99;
}
